@import url(https://fonts.googleapis.com/css?family=Quantico);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/**
 * @name App.css
 * @author Schober Andreas
 * Contains basic CSS needed for the layout and design of the website
 */
/*General CSS*/
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
  font-family: "Quantico";
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 1em;
}

body > * {
  text-align: center;
  vertical-align: middle;
}
h1 {
  letter-spacing: 0.4em;
}
hr {
  border-width: 4px;
}

.search-bar {
  text-align: center;
}

.search-bar input {
  width: 75%;
  border: 1px solid black;
}

.video-item img {
  max-width: 64px; /*4em*/
}

.video-detail .details {
  margin-top: 0;
  margin-bottom: 1em;
  border: 1px solid #ddd;
  border-radius: 0px 0px 5px 5px;
  background: white;
}

.embed-responsive-itemn {
  border-radius: 5px 5px 0px 0px;
}

.media-left {
  width: 50px; /*3em*/
  height: 50px;
  border: 1px solid black;
}
.list-group-item,
.searchResultDiv,
.video-detail .details {
  cursor: pointer;
}

.App {
  text-align: center;
}

/* CSS for Searchconsole*/

#search {
  border-radius: 0.5em;
  font-size: 1.2em;
  width: 90%;
  padding: 5px;
  margin: 5px;
  -webkit-transition: width 0.5s ease-in-out;
  transition: width 0.5s ease-in-out;
}

.button-search {
  height: auto;
  width: auto;
}

.flex-basis-50 {
  flex-basis: 50%;
  min-width: 50%;
}

/*WIKIPEDIA*/

.searchResultDiv {
  margin: 1em;
  padding: 1em;
  text-align: left;

  /*   border:1px solid black;
  border-radius: 5px; */
}
.searchResultDiv h3 {
  text-decoration: none;
}
.searchResultDiv h3 a {
  display: inline-block;
  line-height: 16px;
  text-decoration: none;
}

.searchResultDiv a {
  text-decoration: none;
  color: black;
}
.searchResultDiv a:hover {
  color: blue;
}

.searchResultDiv .link a:hover {
  text-decoration: none;
}
.hidden {
  display: none;
}

/**
 * @name Layout.css
 * @author Schober Andreas
 * Contains the CSS needed for the Flexlayout
 */
.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Header {
  flex: 1 1 100%;
}

.Footer {
  flex: 1 1 100%;
}

.Body {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 100%;
}
.Wikipedia {
  flex: 1 1 100%;
}

.Youtube,
.Spotify,
.div4,
.div5,
.div6 {
  flex: 1 1 50%;
}

